.button {
	font: inherit;
	border: 1px solid transparent;
	border-radius: 3px;
	background: darkgray;
	color: white;
	margin: 0.5rem 1rem;
	padding: 0.25rem 1rem;
	cursor: pointer;
}

.button:active,
.button:hover {
	background-color: #741188;
}

.button:focus {
	outline: none;
}
